import 'vux/src/styles/reset.less';
import './register.scss';
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Datetime from 'vux/src/components/datetime/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import value2name from 'vux/src/filters/value2name.js'


Vue.use(ToastPlugin)

import cookie from 'vux/src/tools/cookie/index.js'


import { checkphone } from 'js/validate.js'

import { fetch, rap } from 'js/fetch.js'

let url = {
getSites: 'api/cities',
postRegister: 'api/register',
getAreas:'api/areas'
}

url = rap(url)
import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
name: '',//用户名
phone: '',//手机号
birthDay: '',//出生日期
childGender: [],//孩子性别
genderList: [{ name: '男', value: '1' }, { name: '女', value: '2' }],
childLike: '',//孩子阅读爱好
password: '',//密码
address: [],//省市区
detailAddress: '',//详细地址
sites: [],//站点
isAgree: false,//是否同意协议
siteslist: [],//站点数据
isCheckTel: false,
isCheckPW: false,
addressData: [],
addressName:''
},
computed: {
isDisabled() {
return false

if (this.isAgree && this.name != '' && this.birthDay != '' && this.childGender.length != 0 && this.childLike != '' && this.address.length != 0 && this.detailAddress != '' && this.isCheckTel === true && this.isCheckPW === true && this.password != '' && this.phone != '') {
return false
} else {
return true
}


}
},
created() {

this.getAreas()
},
methods: {
getName(ids, names){
this.addressName=`${names[0]} ${names[1]} ${names[2]}`
},
getAreas(){

fetch('get', url.getAreas).then(res => {
if (res.status >= 200 && res.status <= 300) {
res.data.forEach((item,index)=>{
let obj={}
obj.value=item.id+''
obj.name=item.name
if(!!item.parent_id){
obj.parent=item.parent_id+''
}

if(item.id=='710000'){
this.addressData.push({
name:'--',
value:'710001',
parent:'710000',
})
this.addressData.push({
name:'--',
value:'710002',
parent:'710001',
})
}

if(item.id=='810000'){
this.addressData.push({
name:'--',
value:'810001',
parent:'810000',
})
this.addressData.push({
name:'--',
value:'810002',
parent:'810001',
})
}

if(item.id=='820000'){
this.addressData.push({
name:'--',
value:'820001',
parent:'820000',
})
this.addressData.push({
name:'--',
value:'820002',
parent:'820001',
})
}

this.addressData.push(obj)
})

} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn'
})
}
})
},
register() {

fetch('post', url.postRegister, {
name: this.name,
password: this.password,
phone: this.phone,
child_gender: this.childGender[0],
child_like: this.childLike,
address:this.addressName,
detail_address:this.detailAddress,
site_id:this.sites[0]
}).then(res => {

if (res.status >= 200 && res.status <= 300) {

cookie.set('token', res.data.token)
this.$vux.toast.show({
text: '注册成功',
onHide() {
window.location.href = "./buy_member.html"
}
})
} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})

},
checkTel(value) {
this.isCheckTel = checkphone(value)
return {
valid: this.isCheckTel,
msg: '请输入合格的手机号'
}
},
checkPW(value) {
this.isCheckPW = value.length >= 6 && value.length <= 12 ? true : false
return {
valid: this.isCheckPW,
msg: '密码应该6-12位'
}
},
getSites(boolean) {
if (boolean) {
console.log(this.address)

fetch('get', url.getSites+'/'+this.address[1]+'/sites').then(res => {
if (res.status >= 200 && res.status <= 300) {
let array = []
res.data.forEach(element => {
array.push({ name: element.name, value: element.id + '' })
});


this.$set(this, 'siteslist', array);











} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn'
})
}
})
}
},
onShow() {

},
onHide() {

},
onChange() {

}
},
components: {
Swiper,
Scroller,
Tab,
TabItem,
Sticky,
Flexbox,
FlexboxItem,
XInput,
Group,
XButton,
Cell,
CheckIcon,
PopupPicker,
XAddress,
Datetime
},
mixins: [mixin]
})



